import {
	Scene,
	Engine,
	Color3,
	Vector3,
	MeshBuilder,
	SceneLoader,
	StandardMaterial,
	PointLight,
	HemisphericLight,
	ActionManager,
	ExecuteCodeAction,
	FreeCamera,
	ArcRotateCamera,
} from "babylonjs";

import "babylonjs-loaders";

export class SceneMain {
	scene: Scene;
	engine: Engine;

	constructor(private canvas: HTMLCanvasElement) {
		this.engine = new Engine(this.canvas, true);
		this.scene = this.CreateScene();

		this.engine.runRenderLoop(() => {
			this.scene.render();
		});
	}

	CreateScene(): Scene {
		const scene = new Scene(this.engine);

		const alpha = 0;
		const beta = Math.PI / 2;
		const radius = 0;
		const target = new Vector3(-10, 5, 0);
		const camera = new ArcRotateCamera("Camera", alpha, beta, radius, target, scene);
		camera.lowerRadiusLimit = camera.upperRadiusLimit = camera.radius;
		camera.attachControl();

		const hemiLight = new HemisphericLight("light", new Vector3(10, 5, 0), scene);
		hemiLight.intensity = 5;

		const myModel = SceneLoader.Append("./assets/meshes/", "classroom.glb", scene);

		// const ground = MeshBuilder.CreateGround("ground", { width: 10, height: 10 }, scene);
		// const xrPromise = scene.createDefaultXRExperienceAsync({
		// 	floorMeshes: [ground]
		// });

		// const box = MeshBuilder.CreateBox("box", {});
		// box.position.x = 0.5;
		// box.position.y = 1;

		// const boxMaterial = new StandardMaterial("material", scene);
		// boxMaterial.diffuseColor = Color3.Random();
		// box.material = boxMaterial;

		// box.actionManager = new ActionManager(scene);
		// box.actionManager.registerAction(
		// 	new ExecuteCodeAction(ActionManager.OnPickTrigger,
		// 		function (evt) {
		// 			// const sourceBox = evt.meshUnderPointer;
		// 			// sourceBox.position.x += 0.1;
		// 			// sourceBox.position.y += 0.1;

		// 			boxMaterial.diffuseColor = Color3.Random();
		// 		}));

		// const ball = MeshBuilder.CreateSphere("ball", { diameter: 1 }, scene);
		// ball.position = new Vector3(0, 1, 0);

		return scene;
	}
}
